import React, { Component, PropTypes } from "react";
import html2canvas from "html2canvas";
import jsPDF from "jspdf";
//import Pie from "./Pie.js";
import Highcharts from "highcharts/highcharts.src.js";
import addHighchartsMore from "highcharts/highcharts-more";
import addVariwideModule from "highcharts/modules/variwide";
import HighchartsReact from "highcharts-react-official";

addHighchartsMore(Highcharts);
addVariwideModule(Highcharts);

class ReportView extends Component {
  constructor(props) {
    super(props);

    //   pdf.addImage(Logo, "PNG", 0, 0, "300px", "300px");

    this.state = {
      chartFig: null,
      chartOptions: {
        series: [
          {
            data: [1, 2, 3]
          }
        ],
        chart: {
          height: 400,
          type: "column"
        },
        xAxis: {
          labels: {
            useHTML: true,
            formatter: function () {
              return "";
            }
          }
        }
      },
      series: [
        {
          name: "Gases",
          data: [
            {
              name: "Argon",
              y: 0.9,
              color: "#3498db"
            },
            {
              name: "Nitrogen",
              y: 78.1,
              color: "#9b59b6"
            },
            {
              name: "Oxygen",
              y: 20.9,
              color: "#2ecc71"
            },
            {
              name: "Trace Gases",
              y: 0.1,
              color: "#f1c40f"
            }
          ]
        }
      ]
    };


  }
  componentDidMount() {
    setTimeout(() => {
      var pdf1 = null;
      const input = document.getElementById("divToPrint");
      /////setTimeout(() => input, 2000);
      html2canvas(input, { useCORS: true }).then((canvas) => {
        let imgWidth = 190;
        let imgHeight = (canvas.height * imgWidth) / canvas.width;
        const imgData = canvas.toDataURL("img/png");
        const pdf = new jsPDF("p", "mm", "a4");
        pdf.addImage(imgData, "PNG", 10, 10, imgWidth, imgHeight);
        //   pdf.text("Octonyan loves jsPDF", 35, 25);
        pdf1 = pdf.output("bloburl");
        // this.setState({ file: pdf1 });
        document.getElementById("test").src = pdf1;

      });
    }, 2000);
  }

  state = {
    height: 200,
    width: 200
  };

  render() {
    const data = [
      {
        name: "Argon",
        y: 0.9,
        color: "#3498db"
      },
      {
        name: "Nitrogen",
        y: 78.1,
        color: "#9b59b6"
      },
      {
        name: "Oxygen",
        y: 20.9,
        color: "#2ecc71"
      },
      {
        name: "Trace Gases",
        y: 0.1,
        color: "#f1c40f"
      }
    ];

    const customLabels = [];
    var counter = 0;
    return (
      <div>
        <div style={{ height: 0, overflow: "hidden" }}>
          <div className="container" id="divToPrint">
            <div>
              <div className="row">
                <div className="col-12">Title of Component</div>
              </div>
              {/* <img src={Img} height="400px" width="400px" />

                          <Pie />*/}
              <div className="col-12">
               <HighchartsReact
                  highcharts={Highcharts}
                  options={this.state.chartOptions}
                />
                {this.state.chartFig}
              </div>
            </div>

            <div className="row py-3">
              <h3>test company</h3>
            </div>

            <div className="row">
              <table
                style={{ textAlign: "left" }}
                className="table table-striped table-bordered"
              >
                <thead>
                  <tr
                    style={{
                      backgroundColor: "#4a7bc4",
                      color: "#fff"
                    }}
                  >
                    {Object.keys(data[0]).map(function(keyName, keyIndex) {
                      return (
                          <th scope="col">
                            {keyName}
                          </th>
                      )
                    })
                    }

                  </tr>
                </thead>
                <tbody>
                {
                data.map(val =>{
                  return(
                      <tr>
                        {
                         Object.keys(val).map(function(keyName, keyIndex) {
                            return (
                             <td>{val[keyName]}</td>
                              )
                       })
                         }
                      </tr>
                  )
                     })
                    }
                </tbody>
              </table>
            </div>
          </div>
        </div>

        <div className="mb5">
          {/* <button onClick={this.printDocument}>Print</button>*/}
        </div>
        <iframe
          id="test"
          style={{ position: "absolute", right: 0 }}
          // src={pdf1}
          //  onLoad={this.printDocument}
          width="70%"
          height="400px"
        />
      </div>
    );
  }
}
export default ReportView;
